"use client"

import { Film, Clapperboard, Zap, TrendingUp, Sparkles, BarChart, Calendar } from "lucide-react"
import Link from "next/link"

export default function FeaturesSection() {
  const features = [
    {
      icon: Calendar,
      title: "Smart Calendar",
      description: "AI-powered content scheduling with optimal posting times and engagement predictions",
      platforms: ["LinkedIn", "Instagram", "YouTube"],
      route: "/smart-calendar"
    },
    {
      icon: Clapperboard,
      title: "AI Video Generation",
      description: "Generate viral videos from scratch with AI and auto-upload to LinkedIn, Instagram, and YouTube",
      platforms: ["LinkedIn", "Instagram", "YouTube"],
      route: "/ai-video-generation"
    },
    {
      icon: Zap,
      title: "Viral Ad Creator",
      description: "Create high-performing AI-powered ads and publish directly to social media platforms",
      platforms: ["LinkedIn", "Instagram", "YouTube"],
      route: "/viral-ad-creator"
    },
    {
      icon: TrendingUp,
      title: "Trending Topic Search",
      description: "Discover what's trending across social media platforms with AI-powered insights",
      platforms: ["LinkedIn", "Instagram", "YouTube"],
      route: "/trending-topic-search"
    }
  ]

  return (
    <section id="features" className="py-20 relative">
      <div className="container mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-16">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">
            Powerful Features for{" "}
            <span className="text-transparent bg-gradient-to-r from-purple-400 to-cyan-400 bg-clip-text">
              Content Creators
            </span>
          </h2>
          <p className="text-lg text-white/60 max-w-2xl mx-auto">
            Everything you need to create, schedule, and analyze viral content across all major platforms
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {features.map((feature, index) => (
            <Link
              key={index}
              href={feature.route}
              className="group relative rounded-2xl border border-gray-700 bg-[#1a1a1a]/80 backdrop-blur-xl p-8 hover:border-purple-500/50 transition-all duration-300"
            >
              <div className="mb-4">
                <div className="inline-flex items-center justify-center rounded-xl bg-gradient-to-br from-purple-500/20 to-cyan-500/20 p-3 ring-1 ring-purple-500/30">
                  <feature.icon className="h-6 w-6 text-purple-400" />
                </div>
              </div>

              <h3 className="text-xl font-bold mb-3 group-hover:text-purple-400 transition-colors">
                {feature.title}
              </h3>

              <p className="text-white/60 mb-4">{feature.description}</p>

              <div className="flex flex-wrap gap-2">
                {feature.platforms.map((platform) => (
                  <span
                    key={platform}
                    className="inline-flex items-center gap-1 rounded-lg bg-purple-500/10 px-3 py-1 border border-purple-500/20 text-xs text-purple-400"
                  >
                    {platform}
                  </span>
                ))}
              </div>

              <div className="absolute top-4 right-4 opacity-0 group-hover:opacity-100 transition-opacity">
                <div className="rounded-full bg-purple-500/20 p-2">
                  <svg
                    className="h-4 w-4 text-purple-400"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M9 5l7 7-7 7"
                    />
                  </svg>
                </div>
              </div>
            </Link>
          ))}
        </div>
      </div>
    </section>
  )
}

